import {
    GET_CART_INFO,
    GET_ALL_ITEM_IN_CART,
    GET_ALL_ITEM_IN_CART_SELECTED
} from '../constants/constants'
import {
    API_URL
} from '../constants/constants'
import axiosClient from 'actions/constants/axiosClient';
// import axios from 'axios'
// const token = localStorage.getItem('token');
// const headers = { Authorization: `Bearer ${token}` }


// get thng tin chi tit gi hng
export const getDetailCart = () => {
    return dispatch => {
        axiosClient({
            method: 'GET',
            url: `${API_URL}/api/carts/mine/items`,
            // headers: headers,
        })
            .then((res) => {
                getCartInfo();
                dispatch({
                    type: GET_ALL_ITEM_IN_CART,
                    payload: res
                })
            })
            .catch(err => console.log(err))
    }
}


// get thng tin gi hng (s lng sn phm, status, s lng item)
export const getCartInfo = () => {
    return dispatch => {
        axiosClient({
            method: 'GET',
            url: `${API_URL}/api/carts/mine/info`,
            // headers: headers,
        })
            .then((res) => {
                dispatch({
                    type: GET_CART_INFO,
                    payload: res
                })
            })
            .catch(err => console.log(err))
    }
}


// thm sn phm vo gi hng
export const addProductToCart = (data) => {
    return axiosClient({
        method: "POST",
        // headers: headers,
        url: `${API_URL}/api/carts/mine/items`,
        data: data,
    })
}


// sa s lng sn phm trong gi hng
export const updateQuantityItem = (data) => {
    return axiosClient({
        method: "PUT",
        // headers: headers,
        url: `${API_URL}/api/carts/mine/items/update`,
        data: data,
    })
}

// xo sn phm trong gi hng
export const deleteItemInCart = (data) => {
    return axiosClient({
        method: "DELETE",
        // headers: headers,
        url: `${API_URL}/api/carts/mine/items/remove?product_id=${data}`,
        data: data,
    })
}

// kim tra s lng trc khi t hng
export const checkQuantityItemInCart = (data) => {
    return axiosClient({
        method: 'POST',
        url: `${API_URL}/api/carts/mine/items/check_quantity`,
        data: data,
        // headers: headers,
    })
}

// chn sn phm trong gi  t hng
export const selectedItemToOrder = (data) => {
    return axiosClient({
        method: 'POST',
        url: `${API_URL}/api/carts/mine/items/selected`,
        params: {product_id: data}
        // headers: headers,
    })
}

