import React from "react";
import "./Footer.css";
import { Link } from "react-router-dom";
import { API_URL } from "actions/constants/constants";

export default function Footer() {
  return (
    <>
      <footer className="footer">
        <div className="grid wide">
          <div className="row">
            <div className="col l-2-4 m-4 c-6">
              <h3 className="footer__heading">Chm sc khch hng</h3>
              <ul className="footer-list">
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    Trung tm tr gip
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    Hng dn mua hng
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    Chnh sch vn chuyn
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col l-2-4 m-4 c-6">
              <h3 className="footer__heading">V chng ti</h3>
              <ul className="footer-list">
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    Gii thiu v Shop
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    Tuyn dng
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    iu khon Shop
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col l-2-4 m-4 c-6">
              <h3 className="footer__heading">TuTu Store</h3>
              <ul className="footer-list">
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    1 V Vn Ngn, TP Th c, TP.HCM
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    18 L Li, TP Sc Trng, tnh Sc Trng
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    Bnh i, Bn Tre
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col l-2-4 m-4 c-6">
              <h3 className="footer__heading">Theo di chng ti</h3>
              <ul className="footer-list">
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    <i className="fab fa-facebook" />
                    Facebook
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    <i className="fab fa-instagram" />
                    Instagram
                  </Link>
                </li>
                <li className="footer-item">
                  <Link to="/" className="footer-link">
                    <i className="fab fa-twitter" />
                    Twitter
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col l-2-4 m-8 c-12">
              <h3 className="footer__heading">Vo ca hng trn ng dng</h3>
              <div className="footer__download">
                <Link to="/" className="footer__download-link">
                  <img
                    className="footer__download-img"
                    src={`${API_URL}/images/qrcode.png`}
                    alt=""
                  />
                </Link>
                <div className="footer__download-apps">
                  <Link to="/" className="footer__download-apps-link">
                    <img
                      className="footer__download-apps-img"
                      src={`${API_URL}/images/googleplay.png`}
                      alt=""
                    />
                  </Link>
                  <Link to="/" className="footer__download-apps-link">
                    <img
                      className="footer__download-apps-img"
                      src={`${API_URL}/images/appstore.png`}
                      alt=""
                    />
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
      <div className="footer__bottom">
        <div className="grid wide">
          <div className="row">
            <div className="col l-12 m-12 c-12">
              <p className="footer__copyright">
                 2023 - Bn quyn thuc TuTu-Store
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
